<template>
  <div id='back'>
    <el-form :model="loginForm" :rules="loginRules" ref="loginForm" label-position="left" label-width="0px"
                 class="login-container">
            <h2 class="title">学生平时表现评分系统</h2>
            <el-form-item prop="account">
                <el-input type="text" v-model="loginForm.account" auto-complete="off" placeholder="请输入账号"></el-input>
            </el-form-item>
            <el-form-item prop="pwd">
                <el-input type="password" v-model="loginForm.pwd" auto-complete="off" placeholder="请输入密码"></el-input>
            </el-form-item>
            <el-row type="flex" justify="center" style="text-align: center">
                <el-col :span="12">
                    <el-radio v-model="loginType" label="1">学生</el-radio>
                </el-col>
                <el-col :span="12">
                    <el-radio v-model="loginType" label="2">老师</el-radio>
                </el-col>
            </el-row>
            <el-checkbox v-model="checked" checked class="remember">记住密码</el-checkbox>
            <el-form-item style="width:100%;">
                <el-button type="primary" class="loginBtn"
                           @click.native.prevent="handleSubmit('loginForm')" :loading="logining">登录
                </el-button>
            </el-form-item>
        </el-form>
  </div>
</template>
<script>
import axios from 'axios'
export default {
  data () {
    return {
      loginType: '1',
      logining: false,
      loginForm: {
        account: '',
        pwd: ''
      },
      loginRules: {
        account: [
          { required: true, message: '请输入账号', trigger: 'blur' }
        ],
        pwd: [
          { required: true, message: '请输入密码', trigger: 'blur' }
        ]
      },
      checked: true
    }
  },
  mounted () {
  },
  methods: {
    handleSubmit (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          const param = {
            loginType: this.loginType,
            accountName: this.loginForm.account,
            accountPassword: this.loginForm.pwd
          }
          axios({
            method: 'post',
            url: '../user/userLogin',
            data: JSON.stringify(param)
          }).then(res => {
            if (res.data.code == '1') {
              this.$message({
                showClose: true,
                message: '添加採購計劃成功',
                type: 'success'
              })
              this.getData()
            } else {
              this.$message({
                showClose: true,
                message: '添加採購計劃失敗',
                type: 'error'
              })
            }
          })
        } else {
          return false
        }
      })
    }
  }
}
</script>
