<template>
    <div>
        <el-container>
            <el-header>
                <span href="index" style="font-size: 22px">志蓮淨苑香積廚管理信息系統</span>
                <span style="position: absolute;right: 50px;">
                    <el-dropdown style="font-size: 15px;">
                    <span class="el-dropdown-link">
                        下拉菜单<i class="el-icon-arrow-down el-icon--right"></i>
                    </span>
                    <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item>黄金糕</el-dropdown-item>
                        <el-dropdown-item>狮子头</el-dropdown-item>
                        <el-dropdown-item>螺蛳粉</el-dropdown-item>
                        <el-dropdown-item disabled>双皮奶</el-dropdown-item>
                        <el-dropdown-item divided>蚵仔煎</el-dropdown-item>
                    </el-dropdown-menu>
                    </el-dropdown>
                </span>
            </el-header>
            <el-container>
                <el-aside width="200px">
                    <el-menu class="el-menu-vertical-demo"  @select="handleSelect">
                        <el-submenu index="user">
                            <template slot="title">
                                <i class="el-icon-location"></i>
                                <span slot="title">导航一</span>
                            </template>
                            <el-menu-item index="/user/login">
                                <span slot="title">用户管理</span>
                            </el-menu-item>
                            <el-menu-item index="3">
                                <span slot="title">账号管理</span>
                            </el-menu-item>
                            <el-menu-item index="4">
                                <span slot="title">账号管理</span>
                            </el-menu-item>
                        </el-submenu>
                    </el-menu>
                </el-aside>
                <el-main>Main</el-main>
            </el-container>
        </el-container>
    </div>
</template>
<script>
import axios from 'axios'
export default {
  data () {
    return {
      activeIndex: '2'
    }
  },
  mounted () {
  },
  methods: {
    handleSubmit (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          const param = {
            loginType: this.loginType,
            accountName: this.loginForm.account,
            accountPassword: this.loginForm.pwd
          }
          axios({
            method: 'post',
            url: '../user/userLogin',
            data: JSON.stringify(param)
          }).then(res => {
            if (res.data.code == '1') {
              this.$message({
                showClose: true,
                message: '添加採購計劃成功',
                type: 'success'
              })
              this.getData()
            } else {
              this.$message({
                showClose: true,
                message: '添加採購計劃失敗',
                type: 'error'
              })
            }
          })
        } else {
          return false
        }
      })
    },
    handleSelect (key, keyPath) {
    //   console.log(window.location.href)
    //   return
    //   window.location.href = keyPath
    }
  }
}
</script>
<style scoped>
    html,body,#app,.el-container{
        /*设置内部填充为0，几个布局元素之间没有间距*/
        padding: 0px;
        /*外部间距也是如此设置*/
        margin: 0px;
        /*统一设置高度为100%*/
        height: 100%;
    }
    .el-header{
        background-color: #B3C0D1;
        color: #333;
        text-align: center;
        line-height: 60px;
    }

    .el-aside {
        background-color: #D3DCE6;
        color: #333;
        text-align: center;
        /*line-height: 200px;*/
    }

    .el-main {
        background-color: #E9EEF3;
        color: #333;
        text-align: center;
        /*line-height: 160px;*/
    }
</style>
